<template>
  <Row class="page3">
    <Col :span="7">
      <div class="left">
        <span class='title'><span class="title-text">薪酬模式可视分析</span></span>
        <span class="angle1"></span>
        <span class="angle2"></span>
        <span class="angle3"></span>
        <span class="angle4"></span>
        <div class="left1" style="height:50%;">
          <chartSalaryStack :data="salaryStackData" />
        </div>
        <div class="left1" style="height:50%;">
          <chartSalaryDonut :data="salaryDonutData" />
        </div>
      </div>
    </Col>

    <Col :span="10" style="padding:0 1%;">
      <div class="center-top">
        <span class='title'><span class="title-text">地域招聘热力分布</span></span>
        <china-map :data="citySummaryData" @region-click="handleRegionClick" />
      </div>
      <div class="center-bottom">
        <span class='title'><span class="title-text">地域招聘画像</span></span>
        <span class="angle1"></span>
        <span class="angle2"></span>
        <span class="angle3"></span>
        <span class="angle4"></span>
        <Row class="bottom-radars">
          <Col span="12">
            <RegionSalaryChart :data="topSalaryCities" />
          </Col>
          <Col span="12">
            <RegionCountChart :data="topCountCities" />
          </Col>
        </Row>
      </div>
    </Col>

    <Col :span="7">
      <div class="right-1">
        <div class="right1-1">
          <span class='title'><span class="title-text">行业发展动态</span></span>
          <IndustryTrendChart :data="industryData" />
        </div>
      </div>
      <div class="right-2">
        <div class="right1-1">
          <span class='title'><span class="title-text">新兴职业洞察</span></span>
          <EmergingJobChart :data="topNewJobs" />
        </div>
      </div>
    </Col>

    <!-- 加载和错误提示 -->
    <div v-if="isLoading" class="data-loading">
      <div class="loading-spinner"></div>
      <p>数据加载中...</p>
    </div>

    <div v-if="loadError" class="data-error">
      <div class="error-icon">!</div>
      <p>{{ loadError }}</p>
      <button class="retry-button" @click="loadData">重试</button>
    </div>
  </Row>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { processJobData } from '@/utils/dataProcessor'

// 懒加载图表组件
const chartSalaryStack = () => import('./components/page3/chartSalaryStack.vue')
const chartSalaryDonut = () => import('./components/page3/chartSalaryDonut.vue')
const chinaMap = () => import('./components/page3/chinaMap.vue')
const RegionSalaryChart = () => import('./components/page3/RegionSalaryChart.vue')
const RegionCountChart = () => import('./components/page3/RegionCountChart.vue')
const IndustryTrendChart = () => import('./components/page3/IndustryTrendChart.vue')
const EmergingJobChart = () => import('./components/page3/EmergingJobChart.vue')

// 数据状态变量
const isLoading = ref(true)
const loadError = ref(null)

// 图表数据绑定
const salaryStackData = ref([])
const salaryDonutData = ref([])
const citySummaryData = ref([])
const topSalaryCities = ref([])
const topCountCities = ref([])
const industryData = ref([])
const topNewJobs = ref([])

// 区域点击处理
const handleRegionClick = (region) => {
  console.log('用户点击了区域:', region)
  // 可以在这里实现地图和其他图表的联动
}

// 加载数据方法
const loadData = async () => {
  isLoading.value = true
  loadError.value = null
  
  try {
    // 调用processJobData加载数据
    const jobData = await processJobData()
    
    // 设置各图表数据
    salaryStackData.value = jobData.salary_stack || []
    salaryDonutData.value = jobData.salary_donut || []
    
    // 地图数据
    citySummaryData.value = jobData.cityRankingData?.citySummary || []
    
    // 地域排名数据
    topSalaryCities.value = jobData.cityRankingData?.topSalaryCities || []
    topCountCities.value = jobData.cityRankingData?.topCountCities || []
    
   // 修改page3.vue中的loadData方法，以适配新的数据格式

// 在loadData方法中替换industryData的赋值部分：

// 行业数据 
industryData.value = jobData.industry_parallel || { 
      industryData: [], 
      topEduCodes: [], 
      topExpCodes: [] 
    };
    
    // 新兴职业数据 (处理热门+高薪职位)
    const jobProfiles = jobData.jobProfiles || []
    topNewJobs.value = jobProfiles
      .sort((a, b) => {
        // 确保avgSalary是数字
        const salaryA = typeof a.avgSalary === 'number' ? a.avgSalary : parseFloat(a.avgSalary) || 0;
        const salaryB = typeof b.avgSalary === 'number' ? b.avgSalary : parseFloat(b.avgSalary) || 0;
        return salaryB - salaryA;
      })
      .slice(0, 5)
      .map(job => {
        // 确保数据类型正确
        const avgSalary = typeof job.avgSalary === 'number' ? job.avgSalary : parseFloat(job.avgSalary) || 0;
        return {
          job_title: job.title,
          avg_salary: avgSalary,
          count: job.count
        }
      });
      
    console.log('数据加载完成:', jobData)
    isLoading.value = false
  } catch (error) {
    console.error('数据加载失败:', error)
    loadError.value = `数据加载失败: ${error.message || '未知错误'}`
    isLoading.value = false
  }
}

onMounted(() => {
  loadData()
})
</script>

